img {
    border-radius: 10px;
}

.content {
    width: 100px;
}

.word {
    position: absolute;
    width: 85%;
    height: 6rem;
    line-height: 6rem;
    top: 2.8rem;
    margin-left: 8%;
}

@keyframes textAppear {
    0% {
        opacity: 0.1;
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }
}

.word span {
    letter-spacing: 1rem;
    line-height: 5rem;
    font-weight: 700;
    transition: 5s all;
    animation: textAppear 2s;
    color: rgb(255, 255, 255);
    text-shadow: 3px 3px 20px #ad4d4d,
        -2px 1px 30px #ce4506;
}


.welcom_home span {
    position: relative;
    color: rgb(221, 157, 84);
    text-shadow: 1px 1px 0 #312b2b,
        2px 2px 0 #7288c5,
        3px 3px 0 #7bbbd8,
        4px 4px 0 #4896ca,
        5px 5px 0 #000000,
        6px 6px 0 #ccc,
        7px 7px 0 #ccc,
        8px 8px 0 #ccc,
        9px 9px 0 #ccc,
        10px 10px 10px rgba(0, 0, 0, 0.5);
}

@keyframes jump {
    0%{
        color: #7bbbd8;
    }
    25%{
        color: #ce4506;
    }
    55%{
        color: chartreuse;
    }
    100%{
        color: #000000;
    }
}

